import { NextResponse } from "next/server"
import { db } from "@/lib/db"
import { getAuthUser } from "@/lib/auth-utils"

export async function GET() {
  try {
    const authUser = await getAuthUser()
    if (!authUser) return NextResponse.json({ error: "Unauthorized" }, { status: 401 })

    const items = await db.transaction.findMany({
      where: { userId: authUser.userId },
      orderBy: { createdAt: "desc" },
      take: 100,
    })

    return NextResponse.json(items)
  } catch (error) {
    console.error("[transactions] GET error:", error)
    return NextResponse.json({ error: "Failed to fetch transactions" }, { status: 500 })
  }
}

// Позволяет создавать транзакции (например, импорт/сид), ожидает одиночный объект или массив
export async function POST(request: Request) {
  try {
    const authUser = await getAuthUser()
    if (!authUser) return NextResponse.json({ error: "Unauthorized" }, { status: 401 })

    const payload = await request.json()
    const records = Array.isArray(payload) ? payload : [payload]

    const prepared = records.map((r: any) => ({
      userId: authUser.userId,
      amount: Number(r.amount),
      currency: r.currency ?? "USD",
      method: r.method ?? "crystalpay",
      methodText: r.methodText ?? r.methodText ?? undefined,
      type: r.type ?? "deposit",
      description: r.description ?? undefined,
      status: r.status ?? "completed",
      externalId: r.externalId ?? undefined,
      createdAt: r.createdAt ? new Date(r.createdAt) : undefined,
      completedAt: r.completedAt ? new Date(r.completedAt) : undefined,
    }))

    const created = await db.$transaction(
      prepared.map((data) => db.transaction.create({ data }))
    )

    return NextResponse.json(created, { status: 201 })
  } catch (error) {
    console.error("[transactions] POST error:", error)
    return NextResponse.json({ error: "Failed to create transactions" }, { status: 500 })
  }
}


